@import '../../assets/css/mixins';
@import '../../assets/css/theme';

.dropdown {
  display: inline-flex;
  width: auto;
  cursor: pointer;
  align-items: center;
  font-size: 14px;
  height: 36px;
  padding-right: 25px;

  img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
    border-radius: 50%;
    overflow: hidden;
  }
  @include darkThemeOnly {
    background: url(../../assets/images/arr.svg) right no-repeat;
  }
  @include lightThemeOnly {
    background: url(../../assets/images/arrow/arrow_dark.svg) right no-repeat;
  }
  &_choose {
    border-radius: 5px;
    color: var(--color-button-primary-text);
    padding: 4px 10px 4px 10px;
    font-size: 14px;
    height: 36px;
    display: flex;
    align-items: center;
    background-color: var(--color-button-primary);
    .dropdown {
      @include lightThemeOnly {
        background: url(../../assets/images/arr.svg) right no-repeat;
      }
    }
  }
  &_disabled {
    background: none !important;
    cursor: auto !important;
  }
  &_symbol {
    margin: 0 0 0 5px;
    font-size: 16px;
    font-weight: 600;
    color: var(--color-text-default);
    vertical-align: middle;
    display: inline-block;
  }
}
